<template>
    <div class="page-changeCity">
      <el-row>
        <province/>
      </el-row>
      <el-row>
        <hot title="" :list="hotList"/>
      </el-row>
      <el-row>
        <hot title="" :list="rencentList"/>
      </el-row>
      <el-row>
        <category/>
      </el-row>
    </div>
</template>

<script>
  import Province from '@/components/changeCity/province'
  import hot from '@/components/changeCity/hot'
  import Category from '@/components/changeCity/category'
    export default {
        data(){
          return {
            hotList: ["","","","",""],
            rencentList: ["","","","","",""]
          }
        },
        components: {
          Province,
          hot,
          Category
        }
    }
</script>

<style lang="scss">
  @import '@/assets/css/changecity/hot.scss';
</style>



