<template>
    <div>
      <span class="name"></span>
      <m-select :value="province" title="" :list="provinceList" :showWrapperActive="provinceActive" @change_active="changeProvinceActive" @change="changeProvince" :className="province"/>
      <m-select :value="city" title="" :list="cityList" :showWrapperActive="cityActive" @change_active="changeCityActive" @change="changeCity" :disabled="cityDisable" :className="city"/>
      <span></span>
      <el-select
        v-model="searchWord"
        filterable
        remote
        reserve-keyword
        placeholder=""
        :remote-method="remoteMethod"
        :loading="loading">
        <el-option
          v-for="item in searchList"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
    </div>
</template>

<script>
  // import {mapState, mapActions} from 'vuex'
  import api from '@/api'
  import MSelect from '@/components/changeCity/select'
    export default {
    data (){
      return {
        cityDisable: true,
        searchWord: '',
        provinceActive: false,
        cityActive: false,
        loading: false,
        provinceList: [],
        province:"",
        cityList: [],
        city:"",
        searchList: ["","","","","","","","","","","","","","","","","","","","","","","","","","","","","",""]
      }
    },
      components: {
          MSelect
      },
      created (){
        api.getProvinceList().then(res =>{
          this.provinceList = res.data.data
            .map(item => {
              item.name = item.provinceName
            return item
          })
        })
      },
      methods: {
        changeProvinceActive(flag){
          this.provinceActive = flag
          if(flag){
            this.cityActive = false
          }
        },
        changeCityActive(flag){
          this.cityActive = flag
          if(flag){
            this.provinceActive = false
          }
        },
        remoteMethod (){
          //
        },
        changeProvince(item){
          this.province = item.name
          this.cityDisable = false
          this.cityList = item.cityInfoList
        },
        changeCity(item){
          this.city = item.name
          this.$store.dispatch('setPosition',item)
          console.log(item)
          this.$router.push({name: 'indexPage'})
        }
      }
    }
</script>

<style lang="scss">
  @import '@/assets/css/changecity/select.scss';
  @import '@/assets/css/changecity/iselect.scss';
</style>



