<template>
    <div class="m-detail-list">
      <h3></h3>
      <ul class="m-detail-item">
        <h4>
          {{dealItems.length}}
        </h4>
        <li class="section"  v-for="(item,index) in dealItems" :key="index">
          <div class="section-left">
            <div class="coupon">
              <p><span>{{item.price}}</span></p>
              <span>{{item.title}}</span>
            </div>
            <div class="ti">
              <p>{{item.price}} {{item.title}}</p>
              <span>{{item.saleNum}}</span>
            </div>
          </div>
          <div class="button">
            <button><router-link :to="{name: 'indexPage'}"></router-link></button>
          </div>
        </li>
      </ul>
    </div>
</template>

<script>
    export default {
        props: [
          'dealItems'
        ]
    }
</script>

<style scoped>

</style>



