<template>
    <div class="page-login">
      <div class="login-header">
        <router-link class="logo" :to="{name: 'indexPage'}"></router-link>
      </div>
      <div class="login-panel">
        <div class="banner">
          <img src="https://s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg">
        </div>
        <div class="form">
          <h4 v-if="error" class="tips">{{error}}</h4>
          <p>
            <span></span>
          </p>
          <el-input v-model="userName" :class="{error: error&&!userName}" placeholder="//" class="userName" prefix-icon="profile"></el-input>
          <el-input v-model="password" type="password" placeholder="" :class="{error: error&&!password}" prefix-icon="password"></el-input>
          <div class="foot">
            <a href="#"></a>
          </div>
          <el-button type="primary" class="btn-login" @click="submit"></el-button>
          <p class="reg">
            <span></span>
            <router-link :to="{name: 'register'}"></router-link>
          </p>
          <div class="oauth-wrapper">
            <h3 class="title-wrapper"><span class="title"></span></h3>
            <div class="oauth cf">
              <a class="oauth__link oauth__link--qq" href="/account/connect/tencent" data-mtevent='{"la":"oauth/qq"}'  target="_blank"></a>
              <a class="oauth__link oauth__link--weibo" href="/account/connect/sina" data-mtevent='{"la":"oauth/sina"}' target="_blank"></a>
            </div>
          </div>
        </div>
      </div>
      <footer class="footer">
        <div class="site-info-nav cf">
          <ul>
            <li class="first"><a rel="nofollow" href="#"></a></li>
            <li><a rel="nofollow" href="#"></a></li>
            <li><a rel="nofollow" href="#"></a></li>
            <li><a rel="nofollow" href="#"></a></li>
            <li class="last"><a href="#"></a></li>
          </ul>
        </div>
          <p>
            &copy;<span>2019</span>
            <a href="https://www.meituan.com"></a>
            meituan.com
            <a href="http://www.miibeian.gov.cn/" target="_blank">ICP070791</a>
            11010502025545
          </p>
      </footer>
    </div>
</template>

<script>
  import api from '@/api'
    export default {
        data() {
          return {
            userName: '',
            password: '',
            error: ''
          }
        },
      methods: {
        submit() {
          if(!this.userName){
            this.error = ''
            return false
          }
          if(!this.password){
            this.error = ''
            return false
          }
          api.login({
            params: {
              userName: this.userName,
              password: this.password
            }
          }).then(res => {

            if(res.data.status == 'success'){
              this.$store.commit('setUserName',this.userName)
              this.$router.push({name: 'indexPage'})
            }else {
              this.error = res.data.msg
            }
          })
        }
      }
    }
</script>

<style lang="scss">
  @import "@/assets/css/login/index.scss";
</style>



